.wrapper {
    width: 60%;
    min-width: 300px;
    min-height: 40%;

    background-color: #FFFF;
    color: black;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    border-radius: 20px;

    padding: 20px;
    overflow: hidden;
}

.wrapper i {
    font-size: 32px;
}

.select-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 60%;
    margin: 0 auto;
}

.select-wrapper p, .select-wrapper .select {
    margin-bottom: 40px;
}

.select-wrapper button {
    width: 40%;
    min-width: 200px;
    background-color: #D62839;
    color: white;
    border: none;
    border-radius: 20px;
    padding: 10px 20px;
}