.wrapper {
    width: 60%;
    min-width: 300px;
    min-height: 40%;

    background-color: #FFFF;
    color: black;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    border-radius: 20px;

    padding: 20px;
}

.wrapper i {
    font-size: 32px;
}

.form-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;

    width: 60%;
    min-width: 200px;
}

.form-wrapper button {
    width: 40%;
    min-width: 200px;
    background-color: #D62839;
    color: white;
    border: none;
    border-radius: 20px;
    padding: 10px 20px;
}

.form-wrapper *:not(:last-child) {
    margin-bottom: 20px;
}

.form-wrapper input {
    border: 1px solid #CCC;
    border-radius: 10px;
    padding: 5px 10px;
    width: 200px;
}

.form-wrapper input:focus {
    outline: none;
}

.category-label {
    width: 222px;
}
