@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.1/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
    margin: 0;
    background-color: #141E2F;
    color: white;
    overflow-x: hidden;
    font-family: 'Nunito', sans-serif;
}
body, #root, .App {
    width: 100vw;
    height: 100vh;
}

.App {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.no-content-wrapper {
    height: 60%;
    width: 100%;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.button-wrapper {
    margin-top: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;

    width: 60%;
    min-width: 300px;
}

.button-wrapper button {
    background-color: #D62839;
    color: white;
    padding: 20px 10px;
    border: none;
    border-radius: 20px;
    transition: all 0.3s ease-in;
}

.button-wrapper button i {
    margin-right: 10px;
}

.button-wrapper button:hover {
    background-color: #C61829;
}

select {
    -webkit-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border: 0 !important;

    width: 100%;
    padding: 20px;
}
.select {
    position: relative;
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 3em;
    line-height: 3;
    background-color: #F7F7F7;
}
select {
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 0 2em;
    cursor: pointer;
    margin: 0;
    
}
.select::after {
    content: '\25BC';
    padding: 0 1em;
    color: #141E2F;
    cursor: pointer;
    pointer-events: none;
    transition: .25s all ease;
}

.select:hover::after {
    color: #D62839;
}  

@media screen and (orientation: portrait) {
    .button-wrapper {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .button-wrapper button {
        margin: 10px;
    }
}
.wrapper {
    width: 60%;
    min-width: 300px;
    min-height: 40%;

    background-color: #FFFF;
    color: black;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    border-radius: 20px;

    padding: 20px;

    overflow-y: scroll;
}

.wrapper i {
    font-size: 32px;
}
.show-seeall-item {
    padding-left: 20px;
    display: grid;
    grid-template-rows: 100%;
    grid-template-columns: 40% 40% 20%;
}

.show-seeall-item i {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.wrapper {
    width: 60%;
    min-width: 300px;
    min-height: 40%;

    background-color: #FFFF;
    color: black;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    border-radius: 20px;

    padding: 20px;
}

.wrapper i {
    font-size: 32px;
}

.form-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin: 0 auto;

    width: 60%;
    min-width: 200px;
}

.form-wrapper button {
    width: 40%;
    min-width: 200px;
    background-color: #D62839;
    color: white;
    border: none;
    border-radius: 20px;
    padding: 10px 20px;
}

.form-wrapper *:not(:last-child) {
    margin-bottom: 20px;
}

.form-wrapper input {
    border: 1px solid #CCC;
    border-radius: 10px;
    padding: 5px 10px;
    width: 200px;
}

.form-wrapper input:focus {
    outline: none;
}

.category-label {
    width: 222px;
}

.showtile {
    width: 60%;
    min-width: 300px;
    height: 150px;
    margin: 25px 0;

    background-color: white;
    color: black;

    border-radius: 10px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);

    display: -webkit-flex;

    display: flex;
}

.letters {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    width: 20%;
    min-width: 80px;
    background-color: #030313;
    color: white;
    font-size: calc(12px + 3vmax);

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-transform: translateX(-2px);
            transform: translateX(-2px);
}

.letters p {
    margin: 0;
    font-family: 'Ubuntu', sans-serif;
}

.info {
    height: calc(100% - 40px);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 20px;
}

.info p {
    margin: 0;
}

.show-title {
    font-size: calc(8px + 1vmax);
}

.show-service {
    font-size: calc(4px + 1vmax);
}

@media screen and (max-width: 450px) {
    .showtile {
        height: 100px;
    }
}
.select-category {
    width: 90%;
