@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap');
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.1/font/bootstrap-icons.css");

body {
    margin: 0;
    background-color: #141E2F;
    color: white;
    overflow-x: hidden;
    font-family: 'Nunito', sans-serif;
}
body, #root, .App {
    width: 100vw;
    height: 100vh;
}

.App {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.no-content-wrapper {
    height: 60%;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
}

.button-wrapper {
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around;

    width: 60%;
    min-width: 300px;
}

.button-wrapper button {
    background-color: #D62839;
    color: white;
    padding: 20px 10px;
    border: none;
    border-radius: 20px;
    transition: all 0.3s ease-in;
}

.button-wrapper button i {
    margin-right: 10px;
}

.button-wrapper button:hover {
    background-color: #C61829;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border: 0 !important;

    width: 100%;
    padding: 20px;
}
.select {
    position: relative;
    display: flex;
    width: 100%;
    height: 3em;
    line-height: 3;
    background-color: #F7F7F7;
}
select {
    flex: 1;
    padding: 0 2em;
    cursor: pointer;
    margin: 0;
    
}
.select::after {
    content: '\25BC';
    padding: 0 1em;
    color: #141E2F;
    cursor: pointer;
    pointer-events: none;
    -webkit-transition: .25s all ease;
    -o-transition: .25s all ease;
    transition: .25s all ease;
}

.select:hover::after {
    color: #D62839;
}  

@media screen and (orientation: portrait) {
    .button-wrapper {
        flex-direction: column;
    }
    .button-wrapper button {
        margin: 10px;
    }
}