.wrapper {
    width: 60%;
    min-width: 300px;
    min-height: 40%;

    background-color: #FFFF;
    color: black;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    border-radius: 20px;

    padding: 20px;

    overflow-y: scroll;
}

.wrapper i {
    font-size: 32px;
}